import { merge } from '../utils/index.js';

function set(name, value) {
  var options = merge(arguments[2]);
  var attributes = Object.keys(options).map(function (optionName) {
    var optionValue = options[optionName];

    if (optionValue === true) {
      return ";".concat(optionName);
    }

    var finalOptionName = optionName === 'maxAge' ? 'max-age' : optionName;
    var finalOptionValue = "".concat(optionValue).split(';')[0];
    return ";".concat(finalOptionName, "=").concat(finalOptionValue);
  }).join('');
  return document.cookie = "".concat(encodeURIComponent(name), "=").concat(encodeURIComponent(value)).concat(attributes);
}

export default set;